import {Injectable} from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';

import {QuestionnaireModel} from './questionnaire.model';
import {UUIDGenerator} from '../../utils/UUIDGenerator';

@Injectable({
  providedIn: 'root',
})
export class QuestionnaireStore {

  private data: BehaviorSubject<QuestionnaireModel[]> = new BehaviorSubject(([]));
  public readonly data$: Observable<QuestionnaireModel[]> = this.data.asObservable();

  public add(questionnaireModel: QuestionnaireModel): void {
    this.data.next(this.data.getValue().concat([questionnaireModel]));
  }

  public duplicate(id: string): void {
    const row: QuestionnaireModel = {
      ...this.findById(id),
      uuid: UUIDGenerator.generate()
    };
    if (row) {
      this.add(row);
    }
  }

  public remove(id: string): boolean {
    const row: QuestionnaireModel = this.findById(id);
    if (row) {
      const data: QuestionnaireModel[] = this.data.getValue().filter(questionnaireModel => questionnaireModel.uuid !== row.uuid);
      this.data.next([...data]);
      return true;
    }
    return false;
  }

  public getAll(): Observable<QuestionnaireModel[]> {
    return this.data$;
  }

  public findById(id: string): QuestionnaireModel {
    return this.data.getValue().find(questionnaireModel => questionnaireModel.uuid === id);
  }

  public update(id: string, model: QuestionnaireModel): void {
    let row: QuestionnaireModel = this.findById(id);
    if (row) {
      row = Object.assign(row, model);
      this.data.getValue().filter(questionnaireModel => questionnaireModel.uuid === id).map(() => row);
    }
  }
}
