import {Component} from '@angular/core';
import {QuestionnaireFactory} from '../questionnaire.factory';
import {QuestionnaireStore} from '../questionnaire.store';

@Component({
  selector: 'app-questionnaire-bar',
  templateUrl: './questionnaire-bar.component.html',
  styleUrls: ['./questionnaire-bar.component.scss']
})
export class QuestionnaireBarComponent{

  constructor(private service: QuestionnaireStore) {
  }

  addQuestion(): void {
    this.service.add(QuestionnaireFactory.create());
  }
}
