import {Component, Input} from '@angular/core';

import {QuestionnaireModel} from '../questionnaire.model';
import {QuestionnaireStore} from '../questionnaire.store';


@Component({
  selector: 'app-questionnaire-block',
  templateUrl: './questionnaire-block.component.html',
  styleUrls: ['./questionnaire-block.component.scss']
})
export class QuestionnaireBlockComponent {
  @Input()
  data: QuestionnaireModel;
  @Input()
  index: number;

  constructor(private service: QuestionnaireStore) {
  }

  duplicateBlock(uuid: string): void {
    this.service.duplicate(uuid);
  }

  deleteBlock(uuid: string): void {
    this.service.remove(uuid);
  }
}
