import {Component, OnDestroy, OnInit} from '@angular/core';
import {QuestionnaireModel} from './questionnaire.model';
import {QuestionnaireFactory} from './questionnaire.factory';
import {QuestionnaireStore} from './questionnaire.store';

@Component({
  selector: 'app-questionnaire',
  templateUrl: './questionnaire.component.html',
  styleUrls: ['./questionnaire.component.css']
})
export class QuestionnaireComponent implements OnInit, OnDestroy {

  constructor(private service: QuestionnaireStore) {
  }

  readonly title = 'CREATE POOL';
  data: QuestionnaireModel[];
  subscriptions$ = [];

  ngOnInit(): void {
    this.addQuestion();
    this.subscriptions$.push(this.service.getAll().subscribe(response => {
      this.data = response;
    }));
  }

  ngOnDestroy(): void {
    this.subscriptions$.forEach(sub => sub.unsubscribe());
  }

  addQuestion(): void {
    this.service.add(QuestionnaireFactory.create());
  }

  save(): void {
    this.subscriptions$.push(this.service.data$.subscribe(data => console.log(data)));
  }
}
