import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {QuestionnaireRoutingModule} from './questionnaire-routing.module';
import {QuestionnaireFormComponent} from './questionnaire-block/questionnaire-form/questionnaire-form.component';
import {QuestionnaireBlockComponent} from './questionnaire-block/questionnaire-block.component';
import {QuestionnaireBarComponent} from './questionnaire-bar/questionnaire-bar.component';
import {QuestionnaireComponent} from './questionnaire.component';
import {MaterialTeestModule} from '../../material.module';
import {NoopAnimationsModule} from '@angular/platform-browser/animations';
