import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {BlockType, QuestionnaireBlock, QuestionnaireModel} from '../../questionnaire.model';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {QuestionnaireStore} from '../../questionnaire.store';

@Component({
  selector: 'app-questionnaire-form',
  templateUrl: './questionnaire-form.component.html',
  styleUrls: ['./questionnaire-form.component.scss']
})
export class QuestionnaireFormComponent implements OnInit, OnDestroy {
  @Input()
  data: QuestionnaireModel;
  form: FormGroup;
  options = [
    {
      type: BlockType.RADIO,
      name: 'Radio',
    },
    {
      type: BlockType.CHECKBOX,
      name: 'Checkbox',
    },
    {
      type: BlockType.INPUT,
      name: 'Input',
    },
    {
      type: BlockType.TEXTAREA,
      name: 'Textarea',
    }
  ];
  // {
  //   type: BlockType.SELECT,
  //   name: 'Select',
  // }
  selectedOptionType: BlockType;
  private subscriptions$ = [];

  constructor(private formBuilder: FormBuilder, private store: QuestionnaireStore) {
  }

  ngOnInit(): void {
    this.selectedOptionType = this.data ? this.data.type : null;
    this.form = this.formBuilder.group({
      type: new FormControl(this.data ? this.data.type : '', Validators.required),
      name: new FormControl(this.data ? this.data.name : '', Validators.required),
      optional: new FormControl(this.data ? this.data.optional : false, Validators.required),
      data: this.formBuilder.array((this.data && this.data.data) ? this.data.data.map(block => this.addOption(block)) : [])
    });
    this.subscriptions$.push(
      this.form.valueChanges.subscribe(() => this.store.update(this.data.uuid, this.form.value))
    );
  }

  ngOnDestroy(): void {
    this.subscriptions$.forEach(sub => sub.unsubscribe());
  }

  changeQuestionType(): void {
    this.optionsArray.clear();
    this.addOptionItem();
  }

  get optionsArray(): FormArray {
    return this.form.get('data') as FormArray;
  }

  addOptionItem(): void {
    this.optionsArray.push(this.addOption());
  }

  removeOption(i: number): void {
    this.optionsArray.removeAt(i);
  }

  private addOption(block?: QuestionnaireBlock): FormGroup {
    return this.formBuilder.group({
      value: block != null ? block.value : '',
    });
  }

}
